module.exports = "import Form from 'react-bootstrap/Form';\n\nfunction SwitchExample() {\n  return (\n    <Form>\n      <Form.Check // prettier-ignore\n        type=\"switch\"\n        id=\"custom-switch\"\n        label=\"Check this switch\"\n      />\n      <Form.Check // prettier-ignore\n        disabled\n        type=\"switch\"\n        label=\"disabled switch\"\n        id=\"disabled-custom-switch\"\n      />\n    </Form>\n  );\n}\n\nexport default SwitchExample;\n"