module.exports = "import Form from 'react-bootstrap/Form';\n\nfunction CheckExample() {\n  return (\n    <Form>\n      {['checkbox', 'radio'].map((type) => (\n        <div key={`default-${type}`} className=\"mb-3\">\n          <Form.Check // prettier-ignore\n            type={type}\n            id={`default-${type}`}\n            label={`default ${type}`}\n          />\n\n          <Form.Check\n            disabled\n            type={type}\n            label={`disabled ${type}`}\n            id={`disabled-default-${type}`}\n          />\n        </div>\n      ))}\n    </Form>\n  );\n}\n\nexport default CheckExample;\n"