module.exports = "import Form from 'react-bootstrap/Form';\n\nfunction CheckReverseExample() {\n  return (\n    <Form>\n      {['checkbox', 'radio'].map((type) => (\n        <div key={`reverse-${type}`} className=\"mb-3\">\n          <Form.Check\n            reverse\n            label=\"1\"\n            name=\"group1\"\n            type={type}\n            id={`reverse-${type}-1`}\n          />\n          <Form.Check\n            reverse\n            label=\"2\"\n            name=\"group1\"\n            type={type}\n            id={`reverse-${type}-2`}\n          />\n          <Form.Check\n            reverse\n            disabled\n            label=\"3 (disabled)\"\n            type={type}\n            id={`reverse-${type}-3`}\n          />\n        </div>\n      ))}\n    </Form>\n  );\n}\n\nexport default CheckReverseExample;\n"